import styled from 'styled-components';

export const LoadingBar = styled.div`
  height: 20px;
  background-color: #d8e4fc;
  margin-top: 8px;
  margin-right: 10px;
  float: left;

  &:nth-child(odd) {
    width: 250px;
  }

  &:nth-child(3n-5) {
    width: 150px;
  }

  &:nth-child(2n-8) {
    width: 150px;
  }
`;